// TODO: Use an actual polyfill. This is gross.
export const polyfillRequestIdleCallback = (window) => {
    if (typeof window.requestIdleCallback === 'undefined') {
        window.requestIdleCallback = (callback, options) => setTimeout(callback, 0);
    }
};
export const polyfillAll = (window) => {
    polyfillRequestIdleCallback(window);
};
